import React from 'react';
import { graphql } from 'gatsby';
import Layout from '../components/layout';
import '../components/bootstrap.css';
import {CustomInput} from "reactstrap";
import {ThemeToggler} from "gatsby-plugin-dark-mode";

const PostTemplate = ({ data }) => {
    const { markdownRemark } = data;
    const { frontmatter, html } = markdownRemark;
    return (
        <ThemeToggler>
            {({ theme, toggleTheme }) => (
        <Layout >
            <h1 style={{paddingTop: "50px"}}>Aman Dave
                <div style={{ float: "right", fontSize: "20px"}}>
                    <CustomInput
                        onChange={e => toggleTheme(e.target.checked ? 'dark' : 'light')}
                        checked={theme === 'dark'}
                        type="switch" id="exampleCustomSwitch" name="customSwitch" label="Dark Mode" />
                </div>
            </h1>
            <section>
                <div
                    style={{fontFamily: "Garamond", marginTop: "50px"}}>
                    <h1>{frontmatter.title}</h1>
                    <span>{frontmatter.date}</span>
                </div>
                <br/>
                <hr/>
                <br/>
                <div style={{fontFamily: "Bookman Old Style"}}
                     dangerouslySetInnerHTML={{ __html: html }} />
            </section>
        </Layout>
            )}
        </ThemeToggler>
    );
};

export default PostTemplate;

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        title
      }
    }
  }
`;
